@import '../../common.scss';
.wrap {
	position: relative;
	margin: 20px auto;
	max-width: 1000px;
}
.bunItem, .bunItem:visited {
	display: inline-block;
	background: white;
	margin: 4px;
	padding: 15px 10px;
	width: 120px;
	text-decoration: none;
	color: $black;
	box-shadow: 2px 2px 0 0 rgba(0,0,0,0.12);
	cursor: pointer;
	position: relative;
}
.bunItem:hover {
	box-shadow: 2px 2px 0 0 rgba(0,0,0,0.22);
}
.bunItem.active {
	border: 2px solid #514E4E;
}
.bunItem.extended {
	padding: 15px 10px 10px 10px;
}
.currentInfo {
	position: absolute;
    top: 0px;
    left: 0;
    color: white;
    padding: 2px 4px;
}
.currentInfoText {
	z-index: 1;
	font-size: 14px;
	position: absolute;
	top: 8px;
	left: 4px;
	width: 30px;
}
.currentInfoTriangle:before {
	content: '';
    position: absolute;
    top: 0;
    left: 0;
    border-top: 30px solid #C1271D;
    border-left: 30px solid #C1272D;
    border-right: 30px solid transparent;
    border-bottom: 30px solid transparent;
}
.bunImg {
	height: 80px;
	width: 80px;
	overflow: hidden;
	margin: 0 auto 6px auto;
}
.bunParty {
	display: inline-block;
	margin-right: 4px;
}
.bunName {
	margin-top: 4px;
    overflow: hidden;
    font-size: 18px;
    font-weight: 600;
}
.bunName.extended {
	height: 40px;
	font-size: 16px;
    font-weight: 100;
}
.bunName.extremeExtended {
	height: 86px;
}



