.Story {
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.Story img {
  margin-top: 30px;
  max-width: 100%;
}

.Story h2 {
  color: #c0504d;
  margin: 30px auto 10px auto;
  line-height: 1.3;
  font-size: 30px;
  padding: 0 20px;
}

.author {
  font-size: 15px;
  margin-left: 10px;
  line-height: 1.6;
}

.date {
  font-size: 16px;
  color: rgba(15, 15, 15, 0.83);
}

.Story h3 {
  color: #c0504d;
  text-align: left;
}

.Story p.subtitle {
  padding: 0 0 0 8px;
  font-weight: 800;
  color: black;
}

.Story p {
  line-height: 1.6;
  text-align: left;
  font-size: 16px;
  padding: 5px 10px;
  color: #363434;
}

@media all and (min-width: 600px) {
  .Story p {
    line-height: 1.8;
    font-size: 18px;
    padding: 5px;
    color: rgba(15, 15, 15, 0.84);
  }
}

p.noStory {
  max-width: 320px;
  margin: 10px auto;
  padding: 10px;
  line-height: 1.4;
  color: #5A5757;
  text-align: center;
  font-size: 16px;
}



